// -       ,   
//       
// filter -     "filters"    
//      ,    (  Object.Entries)
// filter[0] -  , filter[1] - 
//   ProductCardPopup.tsx
export function translateChars(category, type, filter) {	
	const dictionary = {
		"": {
			header: {
				"type": " ",
				"area": ", 2",
				"rooms-count": " ",
			},
			value: {
				"type": {
					"house": "",
					"flat": "",
					"apartment": "",
				},
				get "area"() {return filter[1]},
				get "rooms-count"() {return filter[1]},
			}			
		},
		"": {
			header: {
				"production-year": " ",
				"transmission": " ",
				"body-type": " ",
			},
			value: {
				get "production-year"() {return filter[1]},
				"transmission": {
					"auto": "",
					"mechanic": "",
				},
				"body-type": {
					"sedan": "",
					"universal": "",
					"hatchback": "",
					"suv": "",
					"cupe": "",				
				},
			}
		},
		"": {
			header: {
				"type": " ",
				"ram-value": "  ",
				"screen-size": " ",
				"cpu-type": " ",
			},
			value: {
				"type": {
					"ultra": "",
					"home": " ",
					"gaming": " ",
				},
				get "ram-value"() {return `${filter[1]} `},
				get "screen-size"() {return filter[1]},
				get "cpu-type"() {return `Intel Core ${filter[1]}`},
			}			
		},
		"": {
			header: {
				"type": " ",
				"matrix-resolution": " ",
				"supporting": " ",
			},
			value: {
				"type": {
					"slr": "",
					"digital": "",
					"mirrorless": ""
				},
				get "matrix-resolution"() {return filter[1]},
				"supporting": {
					"HD": "HD",
					"FullHD": "Full HD",
					"full-hd": "Full HD",
					"4K": "4K",
					"5K": "5K",
				},
			}
		},
	}

	switch (type) {
		case 'header':
			const translatedHeader = dictionary[category][type][filter[0]];
			return translatedHeader;
		case 'value':
			const tempValue = dictionary[category][type][filter[0]];
			switch (typeof(tempValue)) {
				case 'number':
				case 'string':					
					return tempValue
				case 'object':
					return tempValue[filter[1]]		
			}
		}	
}

//        
//      (json),   
//   SearchPageReducer.ts
export const dbKeysAdapter = (direction, value) => {			
	const keyNames = {
		category: 'category',
		price: 'price',
		estateType: "type",
		minSquare: "area",
		roomsQuantity: "rooms-count",
		laptopType: "type",
		laptopRamValue: "ram-value",
		laptopDiagonal: "screen-size",				
		laptopProcType: "cpu-type",
		cameraType: "type",
		resolutionMatrix: "matrix-resolution",
		resolutionVideo: "supporting",
		bodyType: "body-type",
		minimalYear: "production-year",
		transmission: "transmission",
	}
	switch(direction) {
		case 'toDB':
			return keyNames[value]					
		case 'toState':
			for (const key in keyNames) {
				if (keyNames[key] === value) {
				  return key;
				}
			}
	}			
}