/**
	    
*/

//          
//     
export function formatPublishDate(timestamp: string): string {
	const timestampNumber = Number(timestamp); //    
	// const timestampNumber = new Date(2023, 9, 11, 11).getTime(); //  
	const currentDate = new Date();
	const publishDate = new Date(timestampNumber);
	const timeDiff = currentDate.getTime() - publishDate.getTime();
	// "1696809600000"  9  2023
	// "1576773899132" 19  2019
	// "1697086800000" 12  2023, 10  

	//   ,  "  "
	const hoursDiff = Math.floor(timeDiff / (1000 * 60 * 60));
	if (hoursDiff === 0) {
		return "  ";
	}

	//    24  ,    
	if (hoursDiff > 0 && hoursDiff < 24) {
		let hoursSuffix;
		if ([1, 21].includes(hoursDiff)) hoursSuffix = "";
		else if ([2, 3, 4, 22, 23].includes(hoursDiff)) hoursSuffix = "";
		else hoursSuffix = "";
		return `${hoursDiff} ${hoursSuffix} `;
	}

	//      ,  - 
	const daysDiff = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
	if (daysDiff > 0 && daysDiff <= 7) {
		let daysSuffix;
		if (daysDiff === 1) daysSuffix = "";
		else if ([2, 3, 4].includes(daysDiff)) daysSuffix = "";
		else daysSuffix = "";
		return `${daysDiff} ${daysSuffix} `;
	}

	//   ()
	const dateOptions: Intl.DateTimeFormatOptions = {
		year: "numeric",
		month: "long",
		day: "numeric",
		timeZone: "UTC",
	};

	//   ,      
	//  "19 "
	if (currentDate.getFullYear() === publishDate.getFullYear()) {
		const shortDateRus = new Date(timestampNumber)
			.toLocaleDateString("ru", dateOptions)
			.replace(/\d+ \.$/, "");
		return shortDateRus;
	}
	//     1 ,   
	//  "19  2019 "
	if (currentDate.getFullYear() - publishDate.getFullYear() > 0) {
		const fullDateRus = new Date(timestampNumber)
			.toLocaleDateString("ru", dateOptions)
			.replace(/\.$/, "");
		return fullDateRus;
	}
}
